import React from 'react'

export default function syros() {
  return (
     <div className='container'>
        <article id='Syros' className='row'>
           <h2 className='col-md-10 col-xs-offset-1 col-xs-10'>Syros</h2>
           <img
              className='col-md-4 offset-md-1'
              src='/Assets/images/syros.jpg'
              alt='Syros'
           />
           <p className='col-md-4 offset-md-2' id='Syros'>
              A favourite Athenian escape and former shipowners' vacation spot
              full of grand mansions, Syros is little known to foreigners.
              Famous for its neoclassical architecture and perfectly preserved
              heritage, its capital, Ermoupolis, was a 13th-century
              Venetian-built town and important trade and industrial centre, and
              boasts giant churches and beautiful villas, a stunning town hall,
              and labyrinthine streets and stairways.
           </p>
        </article>
        <hr />
     </div>
  );
}
